import {
  Pagination,
  PaginationContent,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from '@/components/ui/pagination'

export const PaginationWrapper = ({
  paginationLinkBase,
  current,
  total,
  isFr = true,
}: {
  paginationLinkBase: string
  current: number
  total: number
  isFr?: boolean
}) => {
  const pagesRange = getPageRange(Number(current), total)
  // console.log(pagesRange)
  return (
    <Pagination className="mt-12">
      <PaginationContent>
        <PaginationItem>
          <PaginationPrevious
            isFr={isFr}
            disabled={current == 1}
            href={`${paginationLinkBase}${Number(current) - 1}`}
          />
        </PaginationItem>
        {pagesRange.map((index) => (
          <PaginationItem key={index}>
            <PaginationLink
              href={`${paginationLinkBase}${index}`}
              isActive={index == Number(current)}
            >
              {index}
            </PaginationLink>
          </PaginationItem>
        ))}

        <PaginationItem>
          <PaginationNext
            isFr={isFr}
            href={`${paginationLinkBase}${current + 1}`}
            disabled={Number(current) >= total}
          />
        </PaginationItem>
      </PaginationContent>
    </Pagination>
  )
}

function getPageRange(current: number, total: number): number[] {
  let start: number, end: number

  if (total <= 5) {
    // total pages less than 5, show all
    start = 1
    end = total
  } else if (current <= 2) {
    start = 1
    end = 5
  } else if (current >= total - 1) {
    start = total - 4
    end = total
  } else {
    start = current - 2
    end = current + 2
  }

  return Array.from({ length: end - start + 1 }, (_, i) => start + i).map((i) =>
    Math.ceil(i),
  )
}
