// import { useEffect, useState } from 'react'
import { categoriesAr, categoriesFr } from '@/types'
import Latest from './latest'
import Weekly from './weekly'
import { useIsFr } from '@/hooks/use-is-fr'
import _ from 'lodash'
import { useRef, useEffect } from 'react'
import { useGetTrending } from '@/query/post/getTrendingByTime'
import { useGetPostByCat } from '@/query/post/getLastByCategory'
import { PaginationWrapper } from './pagination'
import { useNavigate, useRouter } from '@tanstack/react-router'
import AdsSlider from './adsSlider'

export default function CategoryComponent({
  categoryId,
  vr,
  code,
  page = '1',
}: {
  categoryId: string
  vr: 'fr' | 'ar'
  code: number
  page: string
}) {
  const navigate = useNavigate()
  const location = useRouter().state.location
  if (location.pathname.includes('page/1') && page === '1') {
    navigate({
      to: vr == 'fr' ? `/news/fr/${categoryId}` : `/news/${categoryId}`,
    })
  }
  // useEffect(() => {
  //   async function fetchData() {
  //     const postsRes = await fetch(
  //       'https://bladiscra.medjdenimadeddine.workers.dev/',
  //     )
  //     const { posts } = await postsRes.json()
  //     setPosts(posts)

  //     setLoading(false)
  //   }
  //   fetchData()
  // }, [])

  // if (loading || !posts) return <PageSkeleton />
  const { data, isLoading } = useGetPostByCat(code, page)

  const { data: dataweek, isLoading: isLoadingweek } = useGetTrending(
    '7j',
    (vr == 'fr' ? 1 : 2).toString(),
  )

  const posts = data?.posts ?? []

  const weekPosts = dataweek?.posts ?? []

  const sliderDev = useRef<HTMLDivElement>(
    null,
  ) as React.RefObject<HTMLDivElement>

  useEffect(() => {
    // sliderDev.current.style.position = 'fixed'

    const handleScroll = _.throttle(() => {
      const side = sliderDev.current
      // console.log(' do window scrollY:', window.scrollY)
      // console.log(side.clientHeight - window.innerHeight)

      if (
        window.innerWidth > 1000 &&
        window.scrollY > side.clientHeight - window.innerHeight + 50
      ) {
        // sliderlist.current.style.transform = `translateY(-${window.scrollY }px)`
        side.style.position = 'sticky'
        side.style.top = `-${side.clientHeight - window.innerHeight + 50}px`
      } else {
        // console.log('undo window scrollY:', window.scrollY)

        side.style.position = ''
        side.style.top = '0px'
      }
    }, 10)

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const isFr = useIsFr()
  const categoryName = isFr
    ? categoriesFr.get(categoryId)?.name
    : categoriesAr.get(categoryId)?.name

  const pages = Math.ceil((data?.total ?? 20) / 20)
  // console.log(pages)

  const paginationLinkBase = `/news${vr == 'fr' ? '/fr' : ''}/${categoryId}/page/`

  return (
    <div
      className="container my-12 flex flex-col lg:flex-row lg:pt-20"
      dir={vr == 'fr' ? 'ltr' : 'rtl'}
    >
      <div className="w-full lg:w-3/5 xl:w-2/3 xl:pe-20">
        <h1 className="text-5xl font-bold mb-8">{categoryName}</h1>
        <Latest posts={posts} vr={vr} loading={isLoading} />
        <PaginationWrapper
          isFr={vr === 'fr'}
          paginationLinkBase={paginationLinkBase}
          current={Number(page)}
          total={pages}
        />
      </div>
      <div className="mt-12 w-full lg:mt-0 lg:w-2/5 lg:ps-10 xl:w-1/3 xl:ps-0">
        <div ref={sliderDev} className="space-y-7">
          <AdsSlider />

          <Weekly
            loading={isLoadingweek}
            posts={weekPosts.slice(0, 10)}
            full
            vr={vr}
            hedding={
              vr == 'fr'
                ? 'Les plus populaires cette semaine'
                : 'الأكثر رواجًا هذا الاسبوع'
            }
          />
        </div>
      </div>
    </div>
  )
}
