'use client'

import { Button } from '@/shared/Button'
import { HeadingWithSubProps } from '@/shared/Heading'
// import { ArrowRightIcon } from '@heroicons/react/24/outline'
import { useNavigate } from '@tanstack/react-router'
import { FC } from 'react'

const lastimg =
  "<svg viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M18.25 32.6667C26.3502 32.6667 32.9167 26.1002 32.9167 18C32.9167 9.89983 26.3502 3.33334 18.25 3.33334C10.1498 3.33334 3.58333 9.89983 3.58333 18C3.58333 26.1002 10.1498 32.6667 18.25 32.6667Z' stroke='currentColor' stroke-width='2.93333' stroke-linecap='round' stroke-linejoin='round'/><path d='M18.25 9.20001V18L24.1167 20.9333' stroke='currentColor' stroke-width='2.93333' stroke-linecap='round' stroke-linejoin='round'/></svg>\n"

interface Props extends Pick<HeadingWithSubProps, 'subHeading' | 'dimHeading'> {
  tabActive: {
    fr: string
    ar: string
  }
  onChangeTab?: (item: string) => void
  rightButtonHref?: string
  isFr?: boolean
}

const SectionTabHeader: FC<Props> = ({
  tabActive,

  onChangeTab,
  isFr,
}) => {
  const navigate = useNavigate()
  const path = isFr ? '/news/fr/$postId' : '/news/$postId'
  // console.log(tabActive)

  const linksFr = new Map([
    ['1h', 'trending-lasthours'],
    ['6h', 'trending-6hours'],
    ['24h', 'trending'],
    ['7 days', 'trending-7days'],
    ['30 days', 'trending-30days'],
  ])

  const linksAr = new Map([
    ['1 ساعة', 'trending-lasthours'],
    ['6 ساعات', 'trending-6hours'],
    ['24 ساعة', 'trending'],
    ['7 أيام', 'trending-7days'],
    ['30 يومًا', 'trending-30days'],
  ])

  const links = isFr ? linksFr : linksAr
  const tabs = isFr ? Array.from(linksFr.keys()) : Array.from(linksAr.keys())
  const last = isFr ? 'Dernières News' : 'آخر الأخبار'

  const handleClickTab = (tab: string) => {
    if (tab === 'last') {
      navigate({
        to: isFr ? '/news/fr/$postId' : '/news/$postId',
        params: { postId: isFr ? 'dernieres-actualites' : 'آخر-الأخبار' },
      })
      return
    }
    navigate({
      to: path,
      params: { postId: links.get(tab) || 'trending' },
    })
    onChangeTab && onChangeTab(tab)
  }

  return (
    <div className="section-tab-header relative mb-2 flex flex-col ">
      <div className="flex grow flex-wrap gap-2">
        {tabActive['fr'] === 'last' ? (
          <Button
            color="lime"
            key={'last'}
            onClick={() => handleClickTab('last')}
            className="cursor-pointer"
          >
            <div
              className="  h-4  xl:w-5 xl:h-5 "
              style={{ color: '#F502E4' }}
              dangerouslySetInnerHTML={{
                __html: lastimg,
              }}
            />
            {last}
          </Button>
        ) : (
          <Button
            outline
            key={'last'}
            onClick={() => handleClickTab('last')}
            className="cursor-pointer"
          >
            <div
              className=" h-4  xl:w-5 xl:h-5 "
              style={{ color: '#F502E4' }}
              dangerouslySetInnerHTML={{
                __html: lastimg,
              }}
            />
            {last}
          </Button>
        )}

        {tabs.map((tab) =>
          tabActive[isFr ? 'fr' : 'ar'] === tab ? (
            <Button
              key={tab}
              color="lime"
              onClick={() => handleClickTab(tab)}
              className="cursor-pointer"
            >
              {tab}
            </Button>
          ) : (
            <Button
              className="cursor-pointer"
              key={tab}
              onClick={() => handleClickTab(tab)}
              outline
            >
              {tab}
            </Button>
          ),
        )}
      </div>
    </div>
  )
}

export default SectionTabHeader
