// import Autoplay from 'embla-carousel-autoplay'

import { useEffect, useState } from 'react'
import Image from './image'

import { AdPreview, fetchAdsFeed } from '@/query/post/getAds'
import PostTypeFeaturedIcon from './PostCards/PostTypeFeaturedIcon'
import SpinLoading from '@/shared/spin-loading'
import ReactPlayer from 'react-player'
import { cn } from '@/lib/utils'
import { Button } from './ui/button'
import { Plus } from 'lucide-react'

function AdsSlider({ isFR }: { isFR: boolean }) {
  const [ad, setAd] = useState<AdPreview | null>(null)
  const [isLoaded, setIsLoaded] = useState(false)

  useEffect(() => {
    if (ad) return
    const fetch = async () => {
      await fetchAdsFeed(1).then((ads) => setAd(ads[0]))
      setIsLoaded(true)
    }
    fetch()
  }, [])

  if (!isLoaded)
    return (
      <div>
        <div className="animate-pulse rounded-3xl overflow-hidden  aspect-16/9 m-1">
          <div className="bg-gray-300 h-full w-full"></div>
        </div>

        <div className="px-4 py-1 space-y-2">
          <div className="bg-gray-300 h-5 w-full rounded"></div>
        </div>

        <Button variant="hero" size="lg" className="gap-2 w-full my-4">
          <Plus className="w-5 h-5" />
          {isFR ? 'Publiez votre annonce' : 'أنشر أعلانك'}
        </Button>
      </div>
    )
  if (!ad) return null
  // console.log(ad)

  return (
    <div className="flex flex-col gap-2 group">
      <div className="relative w-full  rounded-2xl ">{renderPostMedia(ad)}</div>

      <a
        href={ad.link}
        target="_blank"
        rel="noopener noreferrer"
        aria-label={ad.title}
      >
        <p className="text-center text-base sm:text-lg font-medium text-neutral-800 dark:text-neutral-200 line-clamp-2">
          {ad.title}
        </p>
      </a>

      <Button
        variant="hero"
        size="lg"
        className="gap-2 mt-4 mx-0.5 md:text-base"
        asChild
      >
        <a
          target="_blank"
          href={`https://ana.fibladi.com/${isFR ? 'fr' : 'ar'}/ads/create/`}
          className=" "
        >
          <Plus className="w-5 h-5" />
          {isFR ? 'Publiez votre annonce' : 'أنشر أعلانك'}
        </a>
      </Button>
    </div>
  )
}

export default AdsSlider

function renderPostMedia(ad: AdPreview) {
  if (ad.Video) {
    if (!ad.Video.includes('youtu')) {
      return (
        <iframe
          className="aspect-16/9 overflow-hidden "
          src={ad.Video}
          width="100%"
          height="100%"
          allow="autoplay; encrypted-media; picture-in-picture"
          allowFullScreen
          style={{ borderRadius: '12px', border: 'none' }}
        />
      )
    }

    return <YoutubeAd ad={ad} />
  }

  return <AdImage ad={ad} />
}

function AdImage({ ad }: { ad: AdPreview }) {
  return (
    <a
      aria-label={ad.title}
      href={ad.link}
      target="_blank"
      rel="noopener noreferrer"
      className="
            relative block w-full
            aspect-video

            h-auto max-h-[70vh]
            overflow-hidden
            rounded-2xl
            bg-black
            shadow-lg shadow-black/30 transition-shadow
          "
    >
      <Image
        fill
        src={{
          src: `https://images2.fibladi.com/H400/${ad.image.split('.')[0]}.webp`,
          // src: `https://i.pinimg.com/1200x/df/95/39/df9539fbd0000c9b08eefbe13631ddcf.jpg`,
        }}
        alt={ad.title}
        className="object-cover"
      />

      <div className="absolute inset-0 bg-black/25 opacity-0 transition-opacity group-hover:opacity-80" />
    </a>
  )
}

function YoutubeAd({ ad }: { ad: AdPreview }) {
  const [isClicked, setIsClicked] = useState(false)
  const [isPlaying, setIsPlaying] = useState(false)

  return (
    <div className="relative rounded-2xl overflow-hidden aspect-16/9">
      <AdImage ad={ad} />

      {!isClicked && (
        <PostTypeFeaturedIcon
          onClick={() => setIsClicked(true)}
          className="absolute z-30 top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 hover:bg-accent-foreground rounded-full cursor-pointer"
          postType="video"
        />
      )}

      <div
        className={cn(
          'flex items-center justify-center absolute inset-0',
          !isPlaying && !isClicked ? 'opacity-0' : 'opacity-100',
        )}
      >
        <SpinLoading />
      </div>

      <ReactPlayer
        url={ad.Video}
        playing={isClicked}
        style={{
          opacity: isPlaying ? 1 : 0,
        }}
        className="z-20 absolute inset-0   transition-opacity"
        width="100%"
        height="100%"
        onPlay={() => {
          setIsPlaying(true)
          setIsClicked(true)
        }}
        onPause={() => {
          setIsPlaying(false)
          setIsClicked(false)
        }}
        onStart={() => setIsPlaying(true)}
      />
    </div>
  )
}
